.service {
    .projects {
        margin-top: 10.8rem;
        padding: 8.4rem 0;
        @media (max-width: 1200px) {
            margin-top: 7.6rem;
            padding: 7rem 0;
        }
        @media (max-width: 768px) {
            margin-top: 6rem;
            padding: 30px 0;
        }
        .header-block {
            &__text {
                position: relative;
                @media(max-width: 768px) {
                    text-align: center;
                }
                &:before {
                    position: absolute;
                    top: calc(100% + 8px);
                    width: 120px;
                    height: 2px;
                    @media(max-width: 768px) {
                        content: none;
                    }
                }
            }
        }
        &-items {
            margin-top: 0;
            padding-top: 74px;
            @media(max-width: 768px) {
                padding-top: 60px;
            }
            &.off-controls {
                .projects-buttons {
                    display: none;
                }
            }
            .projects-buttons {
                display: flex;
                position: absolute;
                top: 15px;
                right: 0;
                .article-slider__btn {
                    position: static;
                    margin-right: 12px;
                    transform: none;
                    outline: none;
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
        &-item {
            height: auto;
            margin-bottom: 0;
        }
    }
    .btn {
        display: block;
        width: 20rem;
        margin-top: 6.4rem;
        padding: 1.8rem;
        text-decoration: none;
        @media (max-width: 992px) {
            margin: 0 auto;
            margin-top: 4rem;
        }
        &:hover {
            color: $white;
        }
    }
    &__intro {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media(max-width: 992px) {
            flex-direction: column;
        }
        h1 {
            margin-bottom: 2.4rem;
        }
        p {
            font-size: 1.6rem;
        }
        .value {
            display: block;
            font-weight: $font-weight--700;
            color: $orange-dim;
            margin-bottom: 3.8rem;
        }
        .article-slider {
            width: 680px;
            max-width: 100%;
            margin: 0;
            padding: 0;
            padding-bottom: 22px;
            &__btn {
                top: calc(50% - 11px);
            }
        }
        .article-slide {
            &__img {
                width: 100%;
                height: 100%;
                overflow: hidden;
                img {
                    max-width: none;
                    max-height: none;
                    font-family: "object-fit:cover", serif;
                    object-fit: cover;
                }
            }
            &__inner {
                padding-bottom: 71.765%;
                .video-btn {
                    overflow: hidden;
                    img {
                        width: 100%;
                        height: 100%;
                        font-family: "object-fit:cover", serif;
                        object-fit: cover;
                    }
                }
            }
        }
        &--left {
            width: 680px;
            margin-right: 20px;
            @media (max-width: 992px) {
                width: 100%;
                margin-right: 0;
                margin-bottom: 40px;
            }
        }
    }
    &-table {
        padding-bottom: 9.6rem;
        @media (max-width: 768px) {
            padding-bottom: 7rem;
        }
        h2, h3, h4, h5, h6 {
            margin-top: 8.4rem;
            margin-bottom: 4.8rem;
            text-align: center;
        }
        .btn {
            margin: 0 auto;
        }
    }
}